// pages/cashier.js
import { useState, useEffect, useRef } from 'react';
import { useRouter } from 'next/router';
import Layout from '../components/Layout';
import Modal from '../components/Modal';
import theme from '../theme';
import toast from 'react-hot-toast';
import AuthGuard from '../components/AuthGuard';

const API_BASE =
  process.env.NEXT_PUBLIC_API_BASE_URL || 'http://localhost:5000/api';

export default function Cashier() {
  const router = useRouter();

  const [user, setUser] = useState({ name: '', role: '' });
  const [cashierId, setCashierId] = useState(null);

  const [products, setProducts] = useState([]);
  const [cart, setCart] = useState([]);
  const [productId, setProductId] = useState('');
  const [quantity, setQuantity] = useState(1);
  const [discount, setDiscount] = useState(0);

  const [shiftSummary, setShiftSummary] = useState({
    invoice_count: 0,
    total_sales: 0,
    avg_sale: 0,
  });

  const [shiftStart, setShiftStart] = useState(new Date());

  const [lastInvoice, setLastInvoice] = useState(null);
  const [showInvoiceModal, setShowInvoiceModal] = useState(false);
  const [showShiftReport, setShowShiftReport] = useState(false);

  const printRef = useRef(null);

  // ÿ™ÿ≠ŸÖŸäŸÑ ÿßŸÑŸÖÿ≥ÿ™ÿÆÿØŸÖ + ŸÅÿ™ÿ≠ Ÿàÿ±ÿØŸäÿ© ÿ™ŸÑŸÇÿßÿ¶ŸäŸãÿß
  useEffect(() => {
    const token =
      typeof window !== 'undefined'
        ? localStorage.getItem('pharmacy_token')
        : null;
    const storedUser =
      typeof window !== 'undefined'
        ? localStorage.getItem('pharmacy_user')
        : null;

    if (!token || !storedUser) {
      router.replace('/');
      return;
    }

    try {
      const parsed = JSON.parse(storedUser);
      setUser(parsed);

      let idCandidate =
        parsed.id ??
        parsed.user_id ??
        parsed.userId ??
        parsed.uid ??
        (parsed.user && (parsed.user.id || parsed.user.user_id));

      if (idCandidate != null) {
        const num = Number(idCandidate);
        const realId = Number.isNaN(num) ? idCandidate : num;
        setCashierId(realId);

        // üî• ŸÅÿ™ÿ≠ Ÿàÿ±ÿØŸäÿ© ÿ™ŸÑŸÇÿßÿ¶ŸäŸãÿß
        fetch(`${API_BASE}/cashier/start-shift`, {
          method: "POST",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify({ cashier_id: realId })
        });

      } else {
        console.warn('ŸÑŸÖ Ÿäÿ™ŸÖ ÿßŸÑÿπÿ´Ÿàÿ± ÿπŸÑŸâ id ÿØÿßÿÆŸÑ ŸÉÿßÿ¶ŸÜ ÿßŸÑŸÖÿ≥ÿ™ÿÆÿØŸÖ', parsed);
      }
    } catch (err) {
      console.error('Invalid user in localStorage', err);
      router.replace('/');
    }
  }, [router]);

  // ÿ™ÿ≠ŸÖŸäŸÑ ÿßŸÑŸÖŸÜÿ™ÿ¨ÿßÿ™
  useEffect(() => {
    const loadProducts = async () => {
      try {
        const res = await fetch(`${API_BASE}/products`);
        const data = await res.json();
        if (!res.ok) throw new Error(data.message || 'ŸÅÿ¥ŸÑ ÿ™ÿ≠ŸÖŸäŸÑ ÿßŸÑŸÖŸÜÿ™ÿ¨ÿßÿ™');
        setProducts(Array.isArray(data) ? data : []);
      } catch (err) {
        console.error(err);
        toast.error('‚ö†Ô∏è ŸÅÿ¥ŸÑ ÿßŸÑÿßÿ™ÿµÿßŸÑ ÿ®ÿßŸÑÿ≥Ÿäÿ±ŸÅÿ± ŸÑÿ¨ŸÑÿ® ÿßŸÑŸÖŸÜÿ™ÿ¨ÿßÿ™');
      }
    };
    loadProducts();
  }, []);

  useEffect(() => {
    setShiftStart(new Date());
  }, []);
  // üìä ÿ™ÿ≠ŸÖŸäŸÑ ŸÖŸÑÿÆÿµ ÿßŸÑŸàÿ±ÿØŸäÿ© ŸÖŸÜ ÿßŸÑÿ®ÿßŸÉ ÿ•ŸÜÿØ
  useEffect(() => {
    if (!cashierId) return;

    const loadShiftSummary = async () => {
      try {
        const res = await fetch(`${API_BASE}/cashier/shift-summary/${cashierId}`);
        const data = await res.json();

        if (data.success) {
          setShiftSummary({
            invoice_count: data.invoice_count,
            total_sales: data.total_sales,
            avg_sale: data.avg_sale,
          });
        }
      } catch (err) {
        console.error("ÿÆÿ∑ÿ£ ŸÅŸä ÿ™ÿ≠ŸÖŸäŸÑ ŸÖŸÑÿÆÿµ ÿßŸÑŸàÿ±ÿØŸäÿ©:", err);
      }
    };

    loadShiftSummary();
  }, [cashierId]);

  const addToCart = () => {
    if (!productId) {
      toast.error('Ÿäÿ±ÿ¨Ÿâ ÿßÿÆÿ™Ÿäÿßÿ± ŸÖŸÜÿ™ÿ¨');
      return;
    }

    const selected = products.find((p) => p.id === Number(productId));
    if (!selected) {
      toast.error('ÿßŸÑŸÖŸÜÿ™ÿ¨ ÿ∫Ÿäÿ± ŸÖŸàÿ¨ŸàÿØ');
      return;
    }

    if (quantity <= 0) {
      toast.error('ÿßŸÑŸÉŸÖŸäÿ© Ÿäÿ¨ÿ® ÿ£ŸÜ ÿ™ŸÉŸàŸÜ 1 ÿ£Ÿà ÿ£ŸÉÿ´ÿ±');
      return;
    }

    setCart((prev) => {
      const exists = prev.find((item) => item.id === selected.id);
      if (exists) {
        return prev.map((item) =>
          item.id === selected.id
            ? { ...item, quantity: item.quantity + quantity }
            : item
        );
      }
      return [
        ...prev,
        {
          id: selected.id,
          name: selected.name,
          price: Number(selected.price),
          quantity,
        },
      ];
    });

    setProductId('');
    setQuantity(1);
    toast.success('‚úÖ ÿ™ŸÖÿ™ ÿßŸÑÿ•ÿ∂ÿßŸÅÿ© ŸÑŸÑŸÅÿßÿ™Ÿàÿ±ÿ©');
  };

  const removeItem = (id) => {
    setCart((prev) => prev.filter((item) => item.id !== id));
    toast.success('ÿ™ŸÖ ÿ≠ÿ∞ŸÅ ÿßŸÑŸÖŸÜÿ™ÿ¨ ŸÖŸÜ ÿßŸÑŸÅÿßÿ™Ÿàÿ±ÿ©');
  };

  const total = cart.reduce(
    (sum, item) => sum + item.price * item.quantity,
    0
  );
  const netTotal = Math.max(0, total - (Number(discount) || 0));

  const completeSale = async () => {
    if (cart.length === 0) {
      toast.error("ŸÑÿß ÿ™Ÿàÿ¨ÿØ ŸÖŸÜÿ™ÿ¨ÿßÿ™ ŸÅŸä ÿßŸÑŸÅÿßÿ™Ÿàÿ±ÿ©");
      return;
    }

    try {
      const createInvoice = await fetch(`${API_BASE}/cashier/new`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ user_id: cashierId })
      });

      const invData = await createInvoice.json();
      if (!invData.success) {
        toast.error("ŸÅÿ¥ŸÑ ÿ•ŸÜÿ¥ÿßÿ° ÿßŸÑŸÅÿßÿ™Ÿàÿ±ÿ©");
        return;
      }

      const invoice_id = invData.invoice_id;

      for (const item of cart) {
        await fetch(`${API_BASE}/cashier/add-item`, {
          method: "POST",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify({
            invoice_id,
            product_id: item.id,
            qty: item.quantity,
            price: item.price
          })
        });
      }

      const checkout = await fetch(`${API_BASE}/cashier/checkout`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          invoice_id,
          method: "cash"
        })
      });

      const checkoutData = await checkout.json();
      if (!checkoutData.success) {
        toast.error("ŸÅÿ¥ŸÑ ÿ•ŸÜŸáÿßÿ° ÿßŸÑŸÅÿßÿ™Ÿàÿ±ÿ©");
        return;
      }

      await fetch(`${API_BASE}/cashier/deduct-stock`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ invoice_id })
      });

      setLastInvoice({
        invoice_code: invoice_id,
        total: checkoutData.total,
        items: cart,
        customer: "ÿπŸÖŸäŸÑ ŸÜŸÇÿØŸä",
        cashier_name: user?.name,
        date: new Date()
      });

      setShowInvoiceModal(true);
      setCart([]);
      setDiscount(0);
      toast.success("ÿ™ŸÖÿ™ ÿπŸÖŸÑŸäÿ© ÿßŸÑÿ®Ÿäÿπ ÿ®ŸÜÿ¨ÿßÿ≠");

      // üîÑ ÿ™ÿ≠ÿØŸäÿ´ ŸÖŸÑÿÆÿµ ÿßŸÑŸàÿ±ÿØŸäÿ© ÿ®ÿπÿØ ÿßŸÑÿ®Ÿäÿπ
      try {
        const res = await fetch(`${API_BASE}/cashier/shift-summary/${cashierId}`);
        const data = await res.json();
        if (data.success) {
          setShiftSummary({
            invoice_count: data.invoice_count,
            total_sales: data.total_sales,
            avg_sale: data.avg_sale,
          });
        }
      } catch (err) {
        console.error("ÿÆÿ∑ÿ£ ŸÅŸä ÿ™ÿ≠ÿØŸäÿ´ ŸÖŸÑÿÆÿµ ÿßŸÑŸàÿ±ÿØŸäÿ©:", err);
      }

    } catch (err) {
      console.error(err);
      toast.error("ÿÆÿ∑ÿ£ ÿ£ÿ´ŸÜÿßÿ° ÿ™ŸÜŸÅŸäÿ∞ ÿπŸÖŸÑŸäÿ© ÿßŸÑÿ®Ÿäÿπ");
    }
  };
  const totalSales = shiftSummary.total_sales;
  const avgSale = shiftSummary.avg_sale;

  // ÿ•ÿ∫ŸÑÿßŸÇ ÿßŸÑŸàÿ±ÿØŸäÿ© ŸÅÿπŸÑŸäŸãÿß
  const handleCloseShift = async () => {
    try {
      const res = await fetch(`${API_BASE}/cashier/close-shift`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ cashier_id: cashierId })
      });

      const data = await res.json();

      if (!data.success) {
        toast.error(data.message || "ŸÅÿ¥ŸÑ ÿ•ÿ∫ŸÑÿßŸÇ ÿßŸÑŸàÿ±ÿØŸäÿ©");
        return;
      }

      toast.success("ÿ™ŸÖ ÿ•ÿ∫ŸÑÿßŸÇ ÿßŸÑŸàÿ±ÿØŸäÿ© ÿ®ŸÜÿ¨ÿßÿ≠");

      setShowShiftReport(true);
      setCart([]);

    } catch (err) {
      console.error(err);
      toast.error("ÿÆÿ∑ÿ£ ÿ£ÿ´ŸÜÿßÿ° ÿ•ÿ∫ŸÑÿßŸÇ ÿßŸÑŸàÿ±ÿØŸäÿ©");
    }
  };

  // ÿ∑ÿ®ÿßÿπÿ© ÿßŸÑŸàÿ±ÿØŸäÿ©
  const handlePrintShiftReport = () => {
    const w = window.open('', '', 'width=800,height=600');
    w.document.write(`
      <html dir="rtl" lang="ar">
        <head>
          <title>ÿ™ŸÇÿ±Ÿäÿ± ŸÜŸáÿßŸäÿ© ÿßŸÑŸàÿ±ÿØŸäÿ©</title>
          <style>
            body { font-family: 'Tajawal', sans-serif; direction: rtl; padding: 20px; }
            h2 { color: ${theme.colors.primary}; text-align: center; }
            table { width: 100%; border-collapse: collapse; margin-top: 15px; }
            th, td { border: 1px solid #ccc; padding: 8px; text-align: center; }
            th { background: #f3f4f6; }
          </style>
        </head>
        <body>${printRef.current.innerHTML}</body>
      </html>
    `);
    w.document.close();
    w.focus();
    w.print();
    w.close();
  };

  // ÿ∑ÿ®ÿßÿπÿ© ÿßŸÑŸÅÿßÿ™Ÿàÿ±ÿ©
  const handlePrintInvoice = () => {
    if (!lastInvoice) return;

    const items = Array.isArray(lastInvoice.items) ? lastInvoice.items : [];

    const html = `
      <html dir="rtl" lang="ar">
        <head>
          <meta charset="utf-8" />
          <title>ŸÅÿßÿ™Ÿàÿ±ÿ© ${lastInvoice.invoice_code}</title>
          <style>
            body { font-family: 'Tajawal', sans-serif; direction: rtl; padding: 20px; }
            h2 { color: ${theme.colors.primary}; text-align: center; margin-bottom: 10px; }
            p { margin: 4px 0; }
            table { width: 100%; border-collapse: collapse; margin-top: 10px; }
            th, td { border: 1px solid #ccc; padding: 6px; text-align: center; font-size: 13px; }
            th { background: #f3f4f6; }
            .total { margin-top: 10px; text-align: left; font-weight: bold; }
          </style>
        </head>
        <body>
          <h2>ÿµŸäÿØŸÑŸäÿ© ÿßŸÑŸÖÿπŸÑŸÖ</h2>
          <p>ŸÅÿßÿ™Ÿàÿ±ÿ© ÿ±ŸÇŸÖ: <strong>${lastInvoice.invoice_code}</strong></p>
          <p>ÿßŸÑÿ™ÿßÿ±ŸäÿÆ: ${new Date(lastInvoice.date).toLocaleString('ar-EG')}</p>
          <p>ÿßŸÑÿπŸÖŸäŸÑ: ${lastInvoice.customer}</p>
          <p>ÿßŸÑŸÉÿßÿ¥Ÿäÿ±: ${lastInvoice.cashier_name || ''}</p>

          <table>
            <thead>
              <tr>
                <th>#</th>
                <th>ÿßŸÑÿµŸÜŸÅ</th>
                <th>ÿßŸÑŸÉŸÖŸäÿ©</th>
                <th>ÿßŸÑÿ≥ÿπÿ±</th>
                <th>ÿßŸÑÿ•ÿ¨ŸÖÿßŸÑŸä</th>
              </tr>
            </thead>
            <tbody>
              ${items
                .map(
                  (it, i) => `
                  <tr>
                    <td>${i + 1}</td>
                    <td>${it.name}</td>
                    <td>${it.qty}</td>
                    <td>${it.price}</td>
                    <td>${it.qty * it.price}</td>
                  </tr>`
                )
                .join('')}
            </tbody>
          </table>

          <p class="total">ÿßŸÑÿ•ÿ¨ŸÖÿßŸÑŸä ÿßŸÑŸÜŸáÿßÿ¶Ÿä: ${lastInvoice.total} ÿ±.ÿ≥</p>
        </body>
      </html>
    `;

    const w = window.open('', '_blank', 'width=800,height=700');
    w.document.open();
    w.document.write(html);
    w.document.close();
    w.focus();
    w.print();
    w.close();
  };
  return (
    <AuthGuard allowedRoles={["cashier"]}>
    <Layout title="ŸÜŸÇÿ∑ÿ© ÿßŸÑÿ®Ÿäÿπ (ÿßŸÑŸÉÿßÿ¥Ÿäÿ±)">
      <div dir="rtl" className="space-y-6">
        
        {/* üíº ŸÖŸÑÿÆÿµ ÿßŸÑŸàÿ±ÿØŸäÿ© */}
        <div className="p-4 card bg-gradient-to-r from-sky-50 to-blue-50">
          <div className="flex items-center justify-between">
            <h2 className="text-lg font-semibold text-gray-700">
              üíº ŸÖŸÑÿÆÿµ ÿßŸÑŸàÿ±ÿØŸäÿ© ÿßŸÑÿ≠ÿßŸÑŸäÿ©
            </h2>
            <div className="flex gap-2">
              <button onClick={handleCloseShift} className="btn btn-primary">
                üßæ ÿ•ÿ∫ŸÑÿßŸÇ ÿßŸÑŸàÿ±ÿØŸäÿ©
              </button>
            </div>
          </div>

          <div className="grid grid-cols-2 gap-3 mt-3 text-sm text-gray-700 md:grid-cols-5">
            <div>
              <p className="text-gray-500">ÿßŸÑŸÉÿßÿ¥Ÿäÿ±</p>
              <p className="font-medium text-gray-900">{user?.name || '‚Äî'}</p>
            </div>
            <div>
              <p className="text-gray-500">ŸàŸÇÿ™ ÿßŸÑÿ®ÿØÿ°</p>
              <p className="font-medium text-gray-900">
                {shiftStart.toLocaleTimeString('ar-SA')}
              </p>
            </div>
            <div>
              <p className="text-gray-500">ÿπÿØÿØ ÿßŸÑŸÅŸàÿßÿ™Ÿäÿ±</p>
              <p className="font-medium text-gray-900">
                {shiftSummary.invoice_count}
              </p>
            </div>
            <div>
              <p className="text-gray-500">ÿ•ÿ¨ŸÖÿßŸÑŸä ÿßŸÑŸÖÿ®Ÿäÿπÿßÿ™</p>
              <p className="font-medium text-green-700">
                {shiftSummary.total_sales} ÿ±.ÿ≥
              </p>
            </div>
            <div>
              <p className="text-gray-500">ŸÖÿ™Ÿàÿ≥ÿ∑ ÿßŸÑŸÅÿßÿ™Ÿàÿ±ÿ©</p>
              <p className="font-medium text-blue-700">
                {shiftSummary.avg_sale} ÿ±.ÿ≥
              </p>
            </div>
          </div>
        </div>

        {/* üßæ ÿßŸÑŸÅÿßÿ™Ÿàÿ±ÿ© + ÿ•ÿ∂ÿßŸÅÿ© ŸÖŸÜÿ™ÿ¨ÿßÿ™ */}
        <div className="grid grid-cols-1 gap-6 lg:grid-cols-3">
          
          {/* ÿ¨ÿØŸàŸÑ ÿßŸÑŸÅÿßÿ™Ÿàÿ±ÿ© */}
          <div className="p-5 card lg:col-span-2">
            <h2 className="mb-3 text-lg font-semibold text-gray-700">
              ÿßŸÑŸÖŸÜÿ™ÿ¨ÿßÿ™ ÿßŸÑŸÖÿ∂ÿßŸÅÿ©
            </h2>

            <table className="w-full text-sm text-right border-t border-gray-100">
              <thead className="text-gray-600 bg-gray-50">
                <tr>
                  <th className="px-3 py-2">ÿßŸÑŸÖŸÜÿ™ÿ¨</th>
                  <th className="px-3 py-2">ÿßŸÑŸÉŸÖŸäÿ©</th>
                  <th className="px-3 py-2">ÿßŸÑÿ≥ÿπÿ±</th>
                  <th className="px-3 py-2">ÿßŸÑÿ•ÿ¨ŸÖÿßŸÑŸä</th>
                  <th />
                </tr>
              </thead>
              <tbody>
                {cart.length > 0 ? (
                  cart.map((item, i) => (
                    <tr key={i} className="border-t hover:bg-gray-50">
                      <td className="px-3 py-2">{item.name}</td>
                      <td className="px-3 py-2">{item.quantity}</td>
                      <td className="px-3 py-2">{item.price} ÿ±.ÿ≥</td>
                      <td className="px-3 py-2 font-semibold text-sky-700">
                        {item.price * item.quantity} ÿ±.ÿ≥
                      </td>
                      <td
                        className="px-3 py-2 text-red-500 cursor-pointer"
                        onClick={() => removeItem(item.id)}
                      >
                        ‚úï
                      </td>
                    </tr>
                  ))
                ) : (
                  <tr>
                    <td
                      colSpan="5"
                      className="py-4 text-center text-gray-500"
                    >
                      ŸÑÿß ÿ™Ÿàÿ¨ÿØ ŸÖŸÜÿ™ÿ¨ÿßÿ™ ŸÖÿ∂ÿßŸÅÿ© ÿ®ÿπÿØ
                    </td>
                  </tr>
                )}
              </tbody>
            </table>
          </div>

          {/* ÿ•ÿ∂ÿßŸÅÿ© ŸÖŸÜÿ™ÿ¨ */}
          <div className="p-5 card">
            <h2 className="mb-3 text-lg font-semibold text-gray-700">
              ÿ•ÿ∂ÿßŸÅÿ© ŸÖŸÜÿ™ÿ¨
            </h2>

            <label className="block mb-2 text-sm text-gray-700">
              ÿßÿÆÿ™ÿ± ÿßŸÑŸÖŸÜÿ™ÿ¨
            </label>
            <select
              value={productId}
              onChange={(e) => setProductId(e.target.value)}
              className="mb-3 select"
            >
              <option value="">ÿßÿÆÿ™ÿ±...</option>
              {products.map((p) => (
                <option key={p.id} value={p.id}>
                  {p.name} ‚Äî {p.price} ÿ±.ÿ≥
                </option>
              ))}
            </select>

            <label className="block mb-2 text-sm text-gray-700">ÿßŸÑŸÉŸÖŸäÿ©</label>
            <input
              type="number"
              min="1"
              value={quantity}
              onChange={(e) =>
                setQuantity(Number(e.target.value) || 1)
              }
              className="mb-3 input"
            />

            <button
              onClick={addToCart}
              className="w-full mb-3 btn btn-primary"
            >
              ‚ûï ÿ•ÿ∂ÿßŸÅÿ© ŸÑŸÑŸÅÿßÿ™Ÿàÿ±ÿ©
            </button>

            <label className="block mb-2 text-sm text-gray-700">ÿÆÿµŸÖ</label>
            <input
              type="number"
              min="0"
              value={discount}
              onChange={(e) =>
                setDiscount(Number(e.target.value) || 0)
              }
              className="mb-3 input"
            />

            <div className="pt-3 text-sm text-gray-600 border-t">
              <p>
                ÿßŸÑÿ•ÿ¨ŸÖÿßŸÑŸä:{' '}
                <span className="font-bold text-gray-900">
                  {total} ÿ±.ÿ≥
                </span>
              </p>
              <p>
                ÿßŸÑÿÆÿµŸÖ:{' '}
                <span className="text-red-600">
                  {Number(discount) || 0} ÿ±.ÿ≥
                </span>
              </p>
              <p className="mt-1 text-lg font-semibold text-sky-700">
                ÿßŸÑÿ•ÿ¨ŸÖÿßŸÑŸä ÿßŸÑŸÜŸáÿßÿ¶Ÿä: {netTotal} ÿ±.ÿ≥
              </p>
            </div>

            <button
              onClick={completeSale}
              className="w-full mt-4 btn btn-secondary"
            >
              üí∞ ÿ•ÿ™ŸÖÿßŸÖ ÿßŸÑÿ®Ÿäÿπ
            </button>
          </div>
        </div>
      </div>

      {/* ŸÖŸàÿØÿßŸÑ ÿ™ŸÇÿ±Ÿäÿ± ÿßŸÑŸàÿ±ÿØŸäÿ© */}
      {showShiftReport && (
        <Modal
          title="ÿ™ŸÇÿ±Ÿäÿ± ŸÜŸáÿßŸäÿ© ÿßŸÑŸàÿ±ÿØŸäÿ©"
          onClose={() => setShowShiftReport(false)}
        >
          <div ref={printRef} className="space-y-2 text-sm text-right">
            <h3 className="mb-3 text-lg font-semibold text-center text-gray-800">
              üìä ÿ™ŸÇÿ±Ÿäÿ± ÿßŸÑŸàÿ±ÿØŸäÿ© ÿßŸÑÿ≠ÿßŸÑŸäÿ©
            </h3>
            <p>
              <strong>ÿßŸÑŸÉÿßÿ¥Ÿäÿ±:</strong> {user?.name || '‚Äî'}
            </p>
            <p>
              <strong>ÿ®ÿØÿßŸäÿ© ÿßŸÑŸàÿ±ÿØŸäÿ©:</strong>{' '}
              {shiftStart.toLocaleTimeString('ar-SA')}
            </p>
            <p>
              <strong>ÿπÿØÿØ ÿßŸÑŸÅŸàÿßÿ™Ÿäÿ±:</strong> {shiftSummary.invoice_count}
            </p>
            <p>
              <strong>ÿ•ÿ¨ŸÖÿßŸÑŸä ÿßŸÑŸÖÿ®Ÿäÿπÿßÿ™:</strong> {shiftSummary.total_sales} ÿ±.ÿ≥
            </p>
            <p>
              <strong>ŸÖÿ™Ÿàÿ≥ÿ∑ ÿßŸÑŸÅÿßÿ™Ÿàÿ±ÿ©:</strong> {shiftSummary.avg_sale} ÿ±.ÿ≥
            </p>
          </div>

          <div className="flex justify-end gap-3 mt-5">
            <button
              onClick={handlePrintShiftReport}
              className="btn btn-secondary"
            >
              üñ®Ô∏è ÿ∑ÿ®ÿßÿπÿ©
            </button>
            <button
              onClick={() => setShowShiftReport(false)}
              className="btn btn-ghost"
            >
              ÿ•ÿ∫ŸÑÿßŸÇ
            </button>
          </div>
        </Modal>
      )}

      {/* ŸÖŸàÿØÿßŸÑ ÿßŸÑŸÅÿßÿ™Ÿàÿ±ÿ© */}
      {showInvoiceModal && lastInvoice && (
        <Modal
          title={`ŸÅÿßÿ™Ÿàÿ±ÿ© ÿ±ŸÇŸÖ ${lastInvoice.invoice_code}`}
          onClose={() => setShowInvoiceModal(false)}
        >
          <div className="space-y-2 text-sm text-right">
            <p><strong>ÿßŸÑÿπŸÖŸäŸÑ:</strong> {lastInvoice.customer}</p>
            <p><strong>ÿßŸÑŸÉÿßÿ¥Ÿäÿ±:</strong> {lastInvoice.cashier_name || '‚Äî'}</p>
            <p><strong>ÿßŸÑÿ™ÿßÿ±ŸäÿÆ:</strong> {new Date(lastInvoice.date).toLocaleString('ar-EG')}</p>

            <table className="w-full mt-2 text-xs border">
              <thead className="bg-gray-50">
                <tr>
                  <th>#</th>
                  <th>ÿßŸÑÿµŸÜŸÅ</th>
                  <th>ÿßŸÑŸÉŸÖŸäÿ©</th>
                  <th>ÿßŸÑÿ≥ÿπÿ±</th>
                  <th>ÿßŸÑÿ•ÿ¨ŸÖÿßŸÑŸä</th>
                </tr>
              </thead>
              <tbody>
                {lastInvoice.items.map((it, i) => (
                  <tr key={i}>
                    <td>{i + 1}</td>
                    <td>{it.name}</td>
                    <td>{it.qty}</td>
                    <td>{it.price}</td>
                    <td>{it.qty * it.price}</td>
                  </tr>
                ))}
              </tbody>
            </table>

            <div className="mt-2 font-semibold text-right text-emerald-700">
              ÿßŸÑÿ•ÿ¨ŸÖÿßŸÑŸä ÿßŸÑŸÜŸáÿßÿ¶Ÿä: {lastInvoice.total} ÿ±.ÿ≥
            </div>
          </div>

          <div className="flex justify-end gap-3 mt-4">
            <button onClick={handlePrintInvoice} className="btn btn-secondary">
              üñ®Ô∏è ÿ∑ÿ®ÿßÿπÿ© ÿßŸÑŸÅÿßÿ™Ÿàÿ±ÿ©
            </button>
            <button onClick={() => setShowInvoiceModal(false)} className="btn btn-ghost">
              ÿ•ÿ∫ŸÑÿßŸÇ
            </button>
          </div>
        </Modal>
      )}
    </Layout>
    </AuthGuard>
  );
}



















